.selector {
    width: 68px;
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.selector__box {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    box-sizing: border-box;
    cursor: pointer;
}

.selector__box_default {
    background: #FFFFFF;
    border: 1px solid #A5AFFB;
}

.selector__box_active {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #6979F8;
    color: #FFFFFF;
}